@charset "UTF-8";
.step-counter {
  background: black;
  position: relative;
  top: 1px;
  margin-right: 5px;
  padding: 5px;
  border: 2px solid black; }

.game {
  float: left; }
  .game > * {
    margin: 5px; }
  .game-info {
    float: left; }
  .game-board {
    float: left; }
  .game-list {
    float: left;
    clear: both; }
  .game .game-board .board {
    transform: perspective(0) rotateX(0) rotateY(0) rotateZ(0);
    transition: all 1s; }
  .game .game-board .rotated-board {
    transform: perspective(471px) rotateX(57deg) rotateY(-3deg) rotateZ(60deg); }
  .game .game-info .button {
    color: #999;
    background: #444;
    margin-top: 5px;
    margin-right: 5px;
    border: 2px solid #666;
    padding: 5px; }
    .game .game-info .button-nuke {
      color: #e00; }
  .game .game-info .actions {
    padding: 0 0 10px 0; }
  .game .game-info .interaction-container {
    display: grid;
    font-family: Verdana;
    border: 1px solid #555;
    grid-template-columns: 50% 50%; }
    .game .game-info .interaction-container .selected {
      display: block;
      padding: 10px; }
    .game .game-info .interaction-container .targeted {
      display: block;
      padding: 10px; }
      .game .game-info .interaction-container .targeted ul {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 3px; }

.auto-cycle {
  white-space: pre;
  top: 1px;
  position: relative;
  color: #222; }
  .auto-cycle span {
    padding-left: 15px; }
  .auto-cycle input {
    display: none;
    border: 1px solid #666;
    background: #222; }
    .auto-cycle input:checked ~ span {
      color: white;
      position: relative; }
      .auto-cycle input:checked ~ span::before {
        position: absolute;
        left: 1px;
        top: -1px;
        content: "âœ“"; }
